@import './commons.scss';

.SideBarMenu {
    background-color: #222;
    color: white;
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    display: flex;
    flex-direction: column;

    @include transition(width);

    &.expanded{
        width: 300px;
    }

    &.collapsed {
        width: 68px;
    }

    .menuButton {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
    
        .hamburgerIcon {
            cursor: pointer;
            align-self: end;
            border: none;
            background-color: transparent;
            color: white;
            padding: 10px;
            width: 68px;
        }
    }
}

